import "./index.scss";
import {history} from "&/history";
import {axios} from "&/axios";
const alert = Modal.alert;
import { Modal } from 'antd-mobile';
import Msgtoggle from "../../mobx";
import {observer} from "mobx-react";

@observer

export default class Pay extends Component{
    constructor(){
        super();
        this.state = {
            houseid:sessionStorage.houseid,
            detail:[],
            banner:[],
            startTime:sessionStorage.startTime.substring(0,10),
            endTime:sessionStorage.endTime.substring(0,10),
            day:sessionStorage.day/(24*60*60*1000),
            pay:null,
            startArr:[],
            endArr:[]
        }
    }
    componentDidMount(){
        axios.post("/airbnb/housedetail",{
            id:this.state.houseid
        }).then(res=>{
            this.setState({
                detail:res.data.result,
                banner:res.data.result.picture_urls.slice(0,5),
                pay:res.data.result.price.substring(1)*1
            })
        });
        axios.get("/airbnb/order",{
            params:{
                phone:sessionStorage.userPhone
            }
        }).then(res=>{
            this.setState({
                startArr:res.data.result.startorder,
                endArr:res.data.result.endorder
            });
        })
    }
    goback=()=>{
        history.go(-1);
    }
    order=()=>{
        var newStart = [];
        this.state.startArr.forEach((item,i)=>{
            if(item.houseid != sessionStorage.houseid){
                newStart.push(item);
            }
        });
        var obj = {
            houseid: this.state.houseid,
            name: this.state.detail.name,
            pic: this.state.detail.picture_url,
            city: this.state.detail.localized_city,
            price: this.state.pay,
            start: this.state.startTime,
            end: this.state.endTime,
            day: this.state.day
        };
        this.state.endArr.push(obj);
        axios.post("/airbnb/payorder",{
            phone:sessionStorage.userPhone,
            startArr:newStart,
            endArr:this.state.endArr
        }).then(res=>{
            Msgtoggle.msgSend();
            if(sessionStorage.checked){
                sessionStorage.checked = sessionStorage.checked * 1 + 1;
            }else{
                sessionStorage.checked = 1;
            }
            this.showAlert();
        })
    }
    goStartOrder = () => {
        sessionStorage.sub = 2;
        history.push("/order");
    }
    goAllOrder = () => {
        sessionStorage.sub = 0;
        history.push("/order");
    }
    showAlert = () => {
        const alertInstance = alert('支付成功', '是否查看全部订单', [
          { text: '返回首页', onPress: () => history.push("/") },
          { text: '查看订单', onPress: () => this.goAllOrder() },
        ]);
    }
    payAlert = () => {
        const alertInstance = alert('是否立即支付', '', [
          { text: '暂不支付', onPress: () => this.goStartOrder() },
          { text: '立即支付', onPress: () => this.order() },
        ]);
    }
    render(){
        const {
            detail,
            banner,
            startTime,
            endTime,
            day,
            pay
        } = this.state;
        return(
            <div className="yuding">
                <div className="ydhd">
                    <i className="iconfont icon-back" onClick={this.goback}></i>
                </div>
                <div className="ydbd">
                    <div className="yd_tit">
                        <div>
                            <h2>{detail.name}</h2>
                            <h2>{detail.price}/{day}晚</h2>
                        </div>
                        <img src={banner[0]} alt=""/>
                    </div>
                    <div className="yd_date">
                        <div>
                            <h5>入住日期</h5>
                            <h2>{startTime}</h2>
                        </div>
                        <div>
                            <h5>退房日期</h5>
                            <h2>{endTime}</h2>
                        </div>
                    </div>
                    <div className="yd_price">
                        <h5>费用明细</h5>
                        <div>
                            <h2>{detail.price} x {day}晚</h2>
                            <h2>￥{pay * day}</h2>
                        </div>
                    </div>
                    <div className="yd_msg">
                        <h5>预订须知</h5>
                        <p>我已知不允许携带宠物，不允许举办派对和活动。</p>
                        <p>如果在预订确认后的48小时内且距离入住日期至少还有24小时取消，整笔预订将获得退款。距离入住时间不到24小时取消预订，首晚房费将不予退还。</p>
                    </div>
                </div>
                <div className="ydft">
                    <h5>￥{pay * day}<span>{day}晚</span></h5>
                    <button onClick={this.payAlert}>立即支付</button>
                </div>
            </div>
        )
    }
}